
.contact-page-container {
    position: absolute;

    z-index: -2;
    
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    
    overflow: hidden;
    
    width: 100%;
    height: 100%;
    
    background-color: var(--landing-background);
}

.contact-page-title {
    position: relative;

    font-size: 2vw;
    font-weight: 600;

    padding: 5px;
    padding-left: 15px;
    padding-right: 15px;

    margin-bottom: 20px;

    text-align: center;

	letter-spacing: 0.5vw;
	font-family: 'Roboto', sans-serif;

    overflow: hidden;

    color: var(--landing-text);

    transition: 0.5s ease;
}

.contact-page-title:hover {
    cursor: pointer;
    color: var(--landing-background);
    background-color: var(--landing-text);
}

.contact-page-title :nth-child(1) {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: var(--landing-text);

    transform: translateY(-100%);

    animation: contact-animation-1 0.5s ease-in forwards;
}

.contact-page-title :nth-child(2) {
    position: absolute;
    top: 0;
    right: 0;
    width: 2px;
    height: 100%;
    background-color: var(--landing-text);

    transform: translateY(-100%);

    animation: contact-animation-2 0.1s linear forwards;
    animation-delay: 0.5s;
}

.contact-page-title :nth-child(3) {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: var(--landing-text);

    transform: translateX(100%);

    animation: contact-animation-3 0.25s linear forwards;
    animation-delay: 0.6s;
}

.contact-page-title :nth-child(4) {
    position: absolute;
    top: 0;
    left: 0;
    width: 2px;
    height: 100%;
    background-color: var(--landing-text);

    transform: translateY(100%);

    animation: contact-animation-4 0.25s ease-out forwards;
    animation-delay: 0.85s;
}

@keyframes contact-animation-1 {
    0% {
        transform: translateX(-100%);
    }
    100% {
        transform: translateX(0%);
    }
}

@keyframes contact-animation-2 {
    0% {
        transform: translateY(-100%);
    }
    100% {
        transform: translateY(0%);
    }
}

@keyframes contact-animation-3 {
    0% {
        transform: translateX(100%);
    }
    100% {
        transform: translateX(0%);
    }
}

@keyframes contact-animation-4 {
    0% {
        transform: translateY(100%);
    }
    100% {
        transform: translateY(0%);
    }
}

.contact-page-email {
    font-size: 1.5vw;
    letter-spacing: 0.5vw;
	font-family: 'Roboto', sans-serif;

    color: var(--landing-text);
}

@media all and (max-width: 1024px) {
    .contact-page-title {
        font-size: 7vw;
        letter-spacing: 1vw;
    }

    .contact-page-email {
        font-size: 5vw;
        letter-spacing: 1.5vw;
    }
}