
.fade-in {
    opacity: 0;
    filter: blur(5px);
    animation: fade-in-anim 0.5s ease-out forwards;
}

.fade-in-bottom {
    transform: translate(0, 100%);
}

.fade-in-left {
    transform: translate(-100%, 0);
}

.title-fade-out {
    animation: fade-out-top-anim 0.5s ease-out forwards;
}

.title-fade-in {
    opacity: 0;
    filter: blur(5px);
    transform: translate(0, 50%);
    animation: fade-in-anim 0.5s ease-out forwards;
}

.hidden {
    opacity: 0;
}

@keyframes fade-out-top-anim {
    to {
        opacity: 0;
        filter: blur(5px);
        transform: translate(0, -50%);
    }
}

@keyframes fade-in-anim {
    to {
        opacity: 1;
        filter: blur(0);
        transform: translate(0, 0);
    }
}