
.abstract-circle {
	position: absolute;
	top: 0;
	left: 0;

	width: 20%;
	aspect-ratio: 1 / 1;

	transform: translate(-50%, -50%);

	border-radius: 50%;

	border: 2px solid var(--landing-text);
}

@media all and (max-width: 1024px) {
	.abstract-circle {
		width: 50%;
	}
}

.jumbotron-main {
	position: absolute;
	display: flex;
    overflow: hidden;

	height: 100%;
	width: 100%;

	padding-left: 0vw;
	padding-right: 0vw;
}

.jumbotron-half {
	display: flex;

	height: 100%;
	width: 40%;

	justify-content: center;
	align-items: center;
}

.jumbotron-padding {
    height: 100%;
    width: 10%;
}

@media all and (max-width: 1024px) {
	.jumbotron-main {
		flex-direction: column;
	}

	.jumbotron-half {
		height: 40%;
		width: 100%;
	}		
	
	.jumbotron-padding {
		height: 10%;
		width: 100%;
	}
}

.title-container {
	position: relative;
	display: flex;
	flex-direction: column;
}

.title-fade-container {
    position: relative;
    display: grid;

    transform: translateY(10%);
}

.title-fade {
    grid-column: 1;
    grid-row: 1;
}

.title-text {
	font-size: 5vw;

	letter-spacing: 1vw;
	font-family: 'Roboto', sans-serif;

	color: rgb(30, 30, 30);
	-webkit-text-stroke: 1px var(--landing-text);
}

.title-subtext {
	text-align: center;

	transform: translateY(-85%);

	font-size: 3vw;
	font-weight: 1000;
	font-family: 'Roboto', sans-serif;

	color: var(--landing-text);
}

@media all and (max-width: 1024px) {
	.title-text {
		font-size: 12vw;
	}	

	.title-subtext {
		font-size: 3vw;
	}
}

.banner-container {
	position: relative;

	display: flex;

	text-align: center;
	justify-content: center;
	align-items: center;

	width: 100%;
	height: 100%;
}

.banner {
	position: relative;
	display: flex;

	text-align: center;
	justify-content: center;
	align-items: center;

	width: 65%;
	height: 100%;

	outline: 2px solid var(--landing-text);
}

.banner-text {
	width: 100%;

	text-align: center;

	font-size: 2.5vw;
	font-weight: 1000;
	font-family: 'Roboto', sans-serif;

    text-indent: 2vw;
	letter-spacing: 2vw;

	color: var(--landing-text);
}

@media all and (max-width: 1024px) {
	.banner {
		width: 50%;
	}

	.banner-text {		
		font-size: 4vw;
		font-weight: 800;

		text-indent: 4vw;
		letter-spacing: 4vw;
	}
}

.call-to-action-text {
    position: absolute;
    top: 50%;

    margin-top: 4em;

    font-size: 1.2vw;
	font-weight: 300;
	font-family: 'Roboto', sans-serif;

	color: var(--landing-text);
}

@media all and (max-width: 1024px) {
	.call-to-action-text {
		font-size: 3vw;
	}
}

.work-together {
	text-decoration: underline;
	transition: 0.5s ease;
}

.work-together:hover {
	letter-spacing: 2px;
	cursor: pointer;
}

.line-dot-animation-container {
	position: absolute;
	right: calc(80% + 20px);

	width: 20%;
	height: 10px;

	overflow-x: hidden;

	display: flex;

	align-items: center;
}

.line-dot-animation-right {
	left: calc(80% + 20px);
	transform: rotate(180deg);
}

.line {
	position: absolute;
	right: 0;

	height: 2px;
	width: 40px;

	opacity: 0;

	background-color: var(--landing-text);

	animation: line-anim-start 0.5s ease forwards;
	animation-delay: 0.1s
}

@media all and (max-width: 1024px) {
	.line-dot-animation-container {
		right: calc(70% + 25px);
		width: 15%;
	}

	.line-dot-animation-right {
		left: calc(70% + 25px);
	}

	.line {
		width: 20px;
	}
}

@keyframes line-anim-start {
	0% {
		right: -40px;
		opacity: 1;
	}

	100% {
		right: 40px;
		opacity: 1;
	}
}

@keyframes line-anim-repeat {
	0% {
		right: 40px;
		opacity: 1;
	}

	40% {
		right: 40px;
		opacity: 1;
	}

	45% {
		right: 20px;
		opacity: 1;
	}

	50% {
		right: 20px;
		opacity: 1;
	}

	55% {
		right: 40px;
		opacity: 1;
	}

	100% {
		right: 40px;
		opacity: 1;
	}
}

@media all and (max-width: 1024px) {
	@keyframes line-anim-start {
		0% {
			right: -20px;
			opacity: 1;
		}
	
		100% {
			right: 20px;
			opacity: 1;
		}
	}

	@keyframes line-anim-repeat {
		0% {
			right: 20px;
			opacity: 1;
		}
	
		40% {
			right: 20px;
			opacity: 1;
		}
	
		45% {
			right: 10px;
			opacity: 1;
		}
	
		50% {
			right: 10px;
			opacity: 1;
		}
	
		55% {
			right: 20px;
			opacity: 1;
		}
	
		100% {
			right: 20px;
			opacity: 1;
		}
	}
}

.dot {
	position: absolute;
	right: 50px;

	width: 10px;
	height: 10px;

	opacity: 0;

	background-color: var(--landing-text);

	animation: dot-anim-start 0.5s ease forwards;
}

@media all and (max-width: 1024px) {
	.dot {
		width: 5px;
		height: 5px;
	}
}

@keyframes dot-anim-start {
	0% {
		right: 0;
		opacity: 1;
	}

	100% {
		right: 100px;
		opacity: 1;
	}
}

@keyframes dot-anim-repeat {
	0% {
		right: 100px;
		opacity: 1;
	}

	40% {
		right: 100px;
		opacity: 1;
	}

	45% {
		right: 80px;
		opacity: 1;
	}

	50% {
		right: 80px;
		opacity: 1;
	}

	55% {
		right: 100px;
		opacity: 1;
	}

	100% {
		right: 100px;
		opacity: 1;
	}
}

@media all and (max-width: 1024px) {
	@keyframes dot-anim-start {
		0% {
			right: 0;
			opacity: 1;
		}
	
		100% {
			right: 50px;
			opacity: 1;
		}
	}
	
	@keyframes dot-anim-repeat {
		0% {
			right: 50px;
			opacity: 1;
		}
	
		40% {
			right: 50px;
			opacity: 1;
		}
	
		45% {
			right: 40px;
			opacity: 1;
		}
	
		50% {
			right: 40px;
			opacity: 1;
		}
	
		55% {
			right: 50px;
			opacity: 1;
		}
	
		100% {
			right: 50px;
			opacity: 1;
		}
	}
}