
.project-page-container {
    position: absolute;

    z-index: -1;

    display: flex;
    flex-direction: column;
    align-items: center;

    overflow: hidden;

    width: 100%;
    height: 100%;

    background-color: var(--landing-text);
}

.abstract-circle-red {
	position: absolute;
	top: 0;
	left: 0;

    z-index: 1;

	width: 20%;
	aspect-ratio: 1 / 1;

	transform: translate(-50%, -50%);

	border-radius: 50%;

    background-color: #CF352B;
}

@media all and (max-width: 1024px) {
	.abstract-circle-red {
		width: 50%;
	}
}

.project-page-padding-container {
    height: 20%;
}

.project-page-projects-container {
    display: flex;
    align-items: center;
    justify-content: center;

    height: 60%;
    width: 100%;
}

.project-page-title-container {
    display: flex;
    align-items: center;

    height: 20%;
    width: 100%;

    padding-left: 10%;

    vertical-align: bottom;
}

.project-page-title {
    font-size: 5vw;

	letter-spacing: 1vw;
	font-family: 'Roboto', sans-serif;

	color: var(--landing-text);
	-webkit-text-stroke: 1px var(--landing-background);
}

@media all and (max-width: 1024px) {
    .project-page-title {
        font-size: 10vw;
    }
}

.project-page-project {
    display: flex;
    justify-content: center;
    align-items: center;
    
    height: 80%;
    width: 100%;
    
    transform: rotateY();
    transform-style: preserve-3d;
}

.project-page-project-title {
    font-size: 2vw;
	letter-spacing: 1vw;
	font-family: 'Roboto', sans-serif;
}

@media all and (max-width: 1024px) {
    .project-page-project-title {
        font-size: 4vw;
    }
}