
.background-container {
    position: absolute;

    display: flex;
    flex-direction: column;
    align-items: center;

    overflow: hidden;

    width: 100%;
    height: 100%;

    pointer-events: none;
}

.background-segment-container {
    display: flex;
    height: 34%;
    width: 100%;
}

.segment {
    width: 50%;
    height: 100%;

    transform: scale(101%);

    background-color: var(--landing-background);
}