
#root {
	height: 100%;
	width: 100%;
}

.App {
	position: fixed;
	top: 0;
	left: 0;

	display: flex;
	align-items: center;
	justify-content: center;

	height: 100vh;
	width: 100%;
	
	background-color: var(--landing-text);
}

/* Taken from Temani Afif at https://stackoverflow.com/questions/65360938/how-to-create-a-text-fill-wave-effect */
.wave {
	position: absolute;
	z-index: 999;

	background:
		radial-gradient(100% 58% at top   ,transparent   99%, var(--primary)) calc(0*100%/3) 0/50.1% 180%,
	  	radial-gradient(100% 58% at bottom, var(--primary) 99%,transparent  ) calc(3*100%/3) 0/50.1% 180%,
	  	radial-gradient(100% 58% at top   ,transparent   99%, var(--primary)) calc(6*100%/3) 0/50.1% 180%,
	  	radial-gradient(100% 58% at bottom, var(--primary) 99%,transparent  ) calc(9*100%/3) 0/50.1% 180%,
	  	linear-gradient(var(--primary), var(--primary)) bottom/100% 0%;
	background-repeat:no-repeat;

	-webkit-background-clip: text;
	background-clip: text;

	color: transparent;

	display: inline-block;
	padding: 20px;

	font-size: 100px;
	font-family: arial;
	-webkit-text-stroke: 2px var(--primary);

	animation:
	  move 1s infinite linear,
	  up   5s infinite linear alternate;
  }
  
  @keyframes move {
	to {
	  background-position:
	  	calc(-6*100%/3) 0,
		calc(-3*100%/3) 0,
		calc(0*100%/3)  0,
		calc(3*100%/3)  0,
		bottom;
	}
  }
  
  @keyframes up {
	to {
	  background-size: 
		50.1% 20%,
		50.1% 20%,
		50.1% 20%,
		50.1% 20%,
		100%  80%;
	}
  }
  
  body {
   background:#f2f2f2;
  }